@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  h5 {
    @apply absolute px-6 duration-200 w-52 bottom-4 md:bottom-8
    group-hover:scale-110 group-hover:text-black;
  }
}

.btn {
  @apply border border-black rounded-sm py-2 px-10 uppercase font-bold font-alata tracking-widest hover:bg-black hover:text-white;
}

.item-container {
  @apply grid grid-rows-1 md:grid-cols-4 gap-6 text-2xl text-white uppercase;
}

.item {
  @apply relative overflow-hidden;
}

.item-gradient {
  @apply absolute top-0 bottom-0 right-0 left-0 bg-gradient-to-b from-transparent
  to-gray-900 group-hover:from-gray-50 group-hover:to-white
  group-hover:opacity-70;
}

#main {
  background-image: url("./images/desktop/image-hero.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

@media (max-width: 576px) {
  #main {
    backround-image: url("./images/mobile/image-hero.jpg");
    background-position: center;
  }
}

.hamburger {
  cursor: pointer;
  width: 24px;
  height: 24px;
  transition: all 0.25s;
  position: relative;
}

.hamburger-top,
.hamburger-middle,
.hamburger-bottom {
  position: absolute;
  width: 24px;
  height: 2px;
  top: 0;
  left: 0;
  background: #fff;
  transform: rotate(0);
  transition: all 0.5s;
}

.hamburger-middle {
  transform: translateY(7px);
}

.hamburger-bottom {
  transform: translateY(14px);
}

.open {
  transform: rotate(90deg);
}

.open .hamburger-top {
  transform: rotate(45deg) translateY(6px) translateX(6px);
}

.open .hamburger-middle {
  display: none;
}

.open .hamburger-bottom {
  transform: rotate(-45deg) translateY(6px) translateX(-6px);
}